import React from 'react'
import styled from 'styled-components'

const Window5 = styled.div`
    height: 70vh;
    width: 100%;
    background-image: url(/static/tomas-jasovsky-178485-unsplash.jpg);
    background-size: cover;
    background-position: center center;   
`;

const ContMail = styled.div`
    height: 30vh;
    text-align: center;
    p {
        margin: 0 auto;
        margin-top: 10vh;
        width: 50%;
        font-family: "fjalla one";
        text-transform: uppercase;
        letter-spacing: 1.5px;
        color: white;
        font-size: 1.5vw;
    }
    a {
        text-decoration: none;
        color: green;
    }
    a:hover {
        color: darkgreen;
    }
`;

const ContactPage = () => (
    <div>
        <Window5></Window5>

        <ContMail>
            <p>For bookings and general enquiries, please get in touch at:<br></br><a href="mailto:info@greenscafe.com.au">info@greenscafe.com.au</a></p>
        </ContMail>
    </div>
)

export default ContactPage



